"use client";

import { useEffect, useState } from "react";
import { useQuery } from "@tanstack/react-query";
import { getActivities, getActivitiesResponse } from "@/http/get-activities";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { Clock, Plus } from "lucide-react";
import {
  KanbanBadge,
  KanbanCard,
  KanbanCardContent,
  KanbanCardFooter,
  KanbanCardFooterBadge,
  KanbanCardHeader,
  KanbanCardTitle,
  KanbanColumn,
  KanbanColumnHeader,
  KanbanTitle,
} from "@/components/shared/kanban";
import { KanbanLoading } from "@/components/shared/kanban-loading";
import {
  ActivityStatus,
  ActivityStatusLabel,
} from "@/communication/responses/enum/ActivityStatus";
import { ScrollArea } from "@/components/ui/scroll-area";

export default function Home() {
  const { data, isLoading } = useQuery({
    queryKey: ["activities"],
    queryFn: () => getActivities(),
  });

  const [columns, setColumns] = useState<getActivitiesResponse[]>([]);

  const ColorBadge = (status: ActivityStatus) => {
    switch (status) {
      case ActivityStatus.Describing:
        return "bg-orange-100 text-orange-700 dark:bg-orange-900/30";
      case ActivityStatus.Doing:
        return "bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-400";
      case ActivityStatus.Done:
        return "bg-cyan-100 text-cyan-700 dark:bg-cyan-900/30 dark:text-cyan-400";
    }
  };

  useEffect(() => {
    if (data) {
      setColumns(data);
    }
  }, [data]);

  return (
    <main className="w-full flex justify-center px-4 py-6 md:px-6 md:py-8">
      <div className="container">
        <div className="mb-6 flex items-center justify-between text-white">
          <div>
            <h1 className="text-2xl font-bold tracking-tight">Kanban Board</h1>
            <p className="">Manage and track your activities</p>
          </div>
          <div className="flex items-center gap-2">
            <Button asChild>
              <Link href="/activity/create">
                <Plus className="h-4 w-4" />
                Criar
              </Link>
            </Button>
          </div>
        </div>
        {isLoading ? (
          <KanbanLoading />
        ) : (
          <ScrollArea className="h-4/7">
            <div className="flex bg-secondary rounded-2xl relative">
              {columns.map((column) => (
                <div key={column.id}>
                  <KanbanColumnHeader className="bg-zinc-500 py-4 sticky top-0 z-10 p-4">
                    <KanbanTitle>{column.title}</KanbanTitle>
                    <KanbanBadge>{column.listActivities.length}</KanbanBadge>
                  </KanbanColumnHeader>
                  <KanbanColumn>
                    {column.listActivities.length === 0 ? (
                      <div className="min-h-[200px] rounded-lg border border-dashed border-gray-200 dark:border-gray-800 flex items-center justify-center">
                        <p className="text-muted-foreground text-sm">
                          Drop tasks here
                        </p>
                      </div>
                    ) : (
                      <div className="space-y-3 relative">
                        {column.listActivities.map((activity) => (
                          <KanbanCard
                            href={`activity/edit/${activity.id}`}
                            key={activity.id}
                          >
                            <KanbanCardHeader
                              id={activity.id.toString()}
                              href={`activity/edit/${activity.id}`}
                            >
                              <KanbanCardTitle>
                                {activity.title}
                              </KanbanCardTitle>
                            </KanbanCardHeader>
                            <KanbanCardContent>
                              {activity.description}
                            </KanbanCardContent>
                            <KanbanCardFooter>
                              <KanbanCardFooterBadge
                                className={ColorBadge(activity.status)}
                              >
                                {ActivityStatusLabel.get(activity.status)}
                              </KanbanCardFooterBadge>
                              <div className="flex items-center text-xs text-zinc-500">
                                <Clock className="mr-1 h-3 w-3" />
                                Saturday, 10:00 AM
                              </div>
                            </KanbanCardFooter>
                          </KanbanCard>
                        ))}
                      </div>
                    )}
                  </KanbanColumn>
                </div>
              ))}
            </div>
          </ScrollArea>
        )}
      </div>
    </main>
  );
}
